import { getUserFollows, getUserFolloweds } from '@/request/api/focus';
export default {
  data() {
    return {
      // 用户昵称
      nickname: '',
      // 用户 id
      id: 0,
      // 页面类型
      type: 0,
      // 展示的列表
      showList: [],
      // 请求数量
      limit: 0
    };
  },

  async mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.id = this.$route.query.id; // 接收类型 1关注 2粉丝

    this.type = this.$route.query.type;
    this.nickname = this.$route.query.nickname;
    this.limit = this.$route.query.limit; // console.log(this.id, this.type, this.nickname, this.limit);
    // 获取用户关注列表

    if (this.type == 1) {
      const res = await getUserFollows(this.id, this.limit, sessionStorage.getItem("cookie")); // console.log('获取用户关注', res);

      this.showList = res.data.follow; // console.log('showList', this.showList);
    } // 获取用户粉丝列表


    if (this.type == 2) {
      const res = await getUserFolloweds(this.id, this.limit, sessionStorage.getItem("cookie")); // console.log('获取用户粉丝', res);

      this.showList = res.data.followeds; // console.log('showList', this.showList);
    }
  },

  methods: {
    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    }

  }
};